body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App-header {
  text-align: center;
  background-color: #282c34;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.select-btns {
   margin: 2em 0em;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
}

.select-btn {
  margin: 0em 1em;
  padding: 0.8em;
  text-decoration: none;
  background: none;
  display: inline-block;
  text-transform: uppercase;
  border-radius: 0.3em;
  border: 0.12em solid rgb(2, 12, 26);
  color: rgb(2, 2, 2);
  background: #7decd0;
  font-weight: bold;
  
}

.select-btn:focus {
  outline: 0;
  background: #5fceb2;
}

.cards {
  text-align: center;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 3em 2em;
}

.search-form {
  margin: 2em 0em 0em 0em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}


input[type=text] {
  border-radius: 0.3em;
  border: 0.12em solid rgb(2, 12, 26);
  width: 30em;
  padding: 0.6em 0.5em;
}

.submit-btn {
  margin: 0em 1em;
  padding: 0.8em;
  text-decoration: none;
  background: none;
  display: inline-block;
  text-transform: uppercase;
  border-radius: 0.3em;
  border: 0.12em solid rgb(2, 12, 26);
  font-weight: 500;
}


.card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.card-image  {
    margin-bottom: 0.6em; 
}

.card-image > img {
    border-radius: 2%;
    border: 0.1em solid black;
}


